



    .dial_test {

        box-sizing: border-box;
        width: 50vw;
        height: 50vw;
        max-width: 600px;
        max-height: 600px;

        position: relative;
        margin: 2rem 0;
        user-select: none;
        overflow: hidden;

        @media (max-width:480px) {
            width: 90vw;
            height: 90vw;
        }

        .dial_test-in {
            width: 100%;
            height: 16px;
            background: white;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }

    }




